<template>
  <div class="favourites">
    <div class="favourites-header">
      <h3 class="text-uppercase" data-aos="fade-up" data-aos-duration="500">Our products</h3>
      <h1 data-aos="fade-up" data-aos-duration="500">The Favourites</h1>
    </div>
    <div class="favourites-wraper">

      <swiper :navigation="true" :lazy="true" :preloadImages="false" :spaceBetween="0" :slidesPerView="2" :slidesPerGroup="1" :scrollbar="{ el: '.js-swiper-scrollbar', draggable: true, snapOnRelease: true }" :loop="false" :loopedSlides="0" :grabCursor="true" :touchDragging="true" :freeMode="true" :modules="modules" :breakpoints="{ '768': { slidesPerView: 3, spaceBetween: 0 }, '992': { slidesPerView: 4, spaceBetween: 0 } }" @swiper="onSwiper" @slideChange="onSlideChange">
        <swiper-slide v-for="(item, index) in items" :key="index" :virtualIndex="index">
          <div class="slide-item">
            <div class="slide-wraper">
              <div class="slide-image">
                <span class="icon-fav" :class="{ 'on': item.standard }" @click="item.standard = !item.standard">
                  <svg width="20" height="17" viewBox="0 0 20 17" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M9.34122 3.07207L9.72744 3.5207L10.1051 3.06488C10.9891 1.99812 12.3132 1.32178 13.7936 1.32178C16.4147 1.32178 18.602 3.72371 18.602 6.44479C18.602 7.04634 18.3274 7.75346 17.8496 8.51105C17.3776 9.25946 16.74 10.0059 16.0895 10.6745C15.4408 11.3412 14.7903 11.9191 14.3011 12.331C14.0568 12.5366 13.8536 12.7 13.7121 12.8116C13.6414 12.8674 13.5861 12.9102 13.5489 12.9388L13.5069 12.9708L13.4967 12.9785L13.4944 12.9803L13.4939 12.9806L13.4939 12.9806L13.4902 12.9834L9.75012 15.8392L6.01411 13.059L6.01407 13.059L6.01363 13.0586L6.01284 13.0581L6.01132 13.0569L6.0011 13.0492L5.95912 13.0172C5.92189 12.9886 5.86661 12.9458 5.79587 12.89C5.65434 12.7784 5.45116 12.6149 5.20691 12.4093C4.71768 11.9975 4.0672 11.4196 3.41852 10.7529C2.76796 10.0843 2.13037 9.33782 1.65835 8.58941C1.18054 7.83183 0.906006 7.12471 0.906006 6.52316C0.906006 3.80208 3.0933 1.40014 5.71434 1.40014C7.16113 1.40014 8.45815 2.0463 9.34122 3.07207Z" />
                  </svg>
                </span>
                <img @load="onImageLoad" class="slide-prod-image" :src="`https://sugrandroses-payments.s3.us-east-1.amazonaws.com/` + item.photo_url" :alt="item.name" loading="lazy">
                <div class="swiper-lazy-preloader"></div>
                <div class="skeleton-box"></div>
              </div>
              <div class="slide-header">
                <h4 class="slide-prod-title">{{ item.name }}</h4>
                <h5 class="slide-prod-desc">Girasoles en Envoltura negra</h5>
                <h5 class="slide-prod-price">
                  {{ $filters.currencyUSD(item.base_price) }}
                </h5>
                <h5 class="slide-prod-options">+5 Colores</h5>
              </div>
            </div>
          </div>
        </swiper-slide>

        <!-- scrollbar -->
        <div class="swiper-scrollbar js-swiper-scrollbar"></div>

      </swiper>

    </div>
  </div>
</template>

<script>
import axios from 'axios';
import AOS from 'aos';
import 'aos/dist/aos.css';
// import Swiper core and required modules
import { Navigation, Pagination, Scrollbar, Autoplay } from 'swiper';// import Swiper core and required modules
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';

export default {
  components: {
    Swiper,
    SwiperSlide
  },
  props: ["src"],
  data: () => ({
    items: [],
    numericFormatConfig: {
      decimalSeparator: ',',
      fractionDigitsMax: 2,
      fractionDigitsMin: 2,
      fractionDigitsSeparator: '',
      thousandsDigitsSeparator: ' '
    },
    loaded: false,
    swiperOption: {
      loop: true,
    }
  }),
  methods: {
    setFavourite: function () {

      let fav = !this.value;

      console.log('fav', fav);
      return fav;

    }
  },
  watch: {
    src: {
      handler() {

      },
      immediate: true,
    }
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    },
    styles(value) {
      return value ? ['on'] : [''];
    }
  },
  created() {
  },
  setup() {

    AOS.init();

    const onSwiper = () => {
    };
    const onSlideChange = () => {
    };

    return {
      onSwiper,
      onSlideChange,
      modules: [Navigation, Pagination, Scrollbar, Autoplay],
    };
  },
  async mounted() {

    const res = await axios.post('https://newapi.sugarandroses.com/api/shops/1/product-ids?currency=1',
      [911, 977]
    ).then(function (response) {
      if (response.statusText == 'OK') {

        let items = response.data;

        if (items.length < 5) {
          let copy = items.slice();
          if (items.length < 3) {
            items = items.concat(copy).concat(copy).concat(copy).concat(copy);
          } else {
            items = items.concat(copy).concat(copy).concat(copy);
          }

        }

        return items;
      }
    }).catch(function () {
      console.log("Lo sentimos, se han detectado algunos errores, intntalo de nuevo.");
    });

    this.items = await res;

  }
};
</script>

<style scoped>
.swiper {
  padding-bottom: 20px;
}

.swiper-scrollbar {
  cursor: pointer;
}

.swiper-horizontal>.swiper-scrollbar,
.swiper-scrollbar.swiper-scrollbar-horizontal {
  left: 15px;
  width: calc(100% - 30px);
}

@media (min-width: 768px) {
  .swiper-scrollbar.swiper-scrollbar-horizontal {
    left: 30px;
    width: calc(100% - 60px);
  }
}

.swiper-container-horizontal>.swiper-scrollbar {
  border-radius: 2px;
  height: 5px;
  width: 200px;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.fade-enter-active {
  transition: opacity 1s ease-in-out;
}

.fade-enter-to {
  opacity: 1;
}

.fade-enter {
  opacity: 0;
}
</style>